import React from 'react';

import NavBar from './components/core/NavBar';
import { Switch, Route } from 'react-router-dom';

import Home from './components/core/Home';
import LatestCard from './components/Card/LatestCard';

import CardContainer from './components/CreateCard/CardContainer';
import AccountContainer from './components/Account/AccountContainer';

const MainRouter = () => {
  return (
    <>
      <NavBar />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/latest" component={LatestCard} />
        <Route exact path="/features">
          <p>Features</p>
        </Route>
        <Route exact path="/regions">
          <p>Regions</p>
        </Route>
        <Route exact path="/account" component={CardContainer} />
        <Route exact path="/login" component={AccountContainer} />
      </Switch>
    </>
  );
};

export default MainRouter;
