import React, { useCallback, useMemo, useState, useEffect } from 'react';
import {
  Typography,
  AppBar,
  Toolbar,
  IconButton,
  Button,
  SwipeableDrawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Tabs,
  Tab,
  SvgIcon,
  Grid,
  //   Link,
  useMediaQuery
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Menu as MenuIcon } from '@material-ui/icons';
import { Link as RouterLink, withRouter } from 'react-router-dom';

import theme from '../../theme';
import SearchBar from './SearchBar';

const useStyles = makeStyles((theme) => ({
  navigationContainer: {},
  logoContainer: {},
  logo: {},
  horizontalNavItems: {
    margin: `0 ${theme.spacing(2)}`,
  },

  navigationItemsDesktop: {
    display: 'flex',
    flexDirection: 'row',
  },
  navigationItemsMobile: {
    display: 'flex',
    flexDirection: 'column',
  },
  verticalTab: {
    '& .MuiTab-wrapper': {
      flexDirection: 'row',
      justifyContent: 'flex-start',
      '& .MuiSvgIcon-root': {
        marginBottom: 0,
        marginRight: theme.spacing(2),
      },
    },
  },
  publicItems: { display: 'flex' },
  accountItems: { display: 'flex', marginLeft: 'auto' },
  offset: theme.mixins.toolbar,
}));

const NavBar = withRouter(({ history, location }, props) => {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const isExtraSmallDevice = useMediaQuery((theme) => {
    return theme.breakpoints.down('sm');
  });
  const isSmallDevice = useMediaQuery((theme) =>
    theme.breakpoints.between('sm', 'smd')
  );
  const isSmallMediumDevice = useMediaQuery((theme) =>
    theme.breakpoints.between('smd', 'md')
  );
  const isMediumDevice = useMediaQuery((theme) =>
    theme.breakpoints.between('md', 'lmd')
  );
  const isLargeMediumDevice = useMediaQuery((theme) =>
    theme.breakpoints.between('lmd', 'lg')
  );
  const isLargeDevice = useMediaQuery((theme) => theme.breakpoints.up('lg'));

  const classes = useStyles();
  // const [menuIsHide, setMenuIsHide] = useState(matches);
  const [drawerIsOpen, setDrawerIsOpen] = useState(false);
  const [linkItemList, setLinkItemList] = useState([
    {
      name: 'Latest',
      label: 'latest',
      svg_path: 'M10,20V14H14V20H19V12H22L12,3L2,12H5V20H10Z',
      type: 'public',
    },
    {
      name: 'Features',
      label: 'features',
      svg_path:
        'M16.23,18L12,15.45L7.77,18L8.89,13.19L5.16,9.96L10.08,9.54L12,5L13.92,9.53L18.84,9.95L15.11,13.18L16.23,18M12,2C6.47,2 2,6.5 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z',
      type: 'public',
    },
    {
      name: 'Regions',
      label: 'regions',
      svg_path:
        'M16.36,14C16.44,13.34 16.5,12.68 16.5,12C16.5,11.32 16.44,10.66 16.36,10H19.74C19.9,10.64 20,11.31 20,12C20,12.69 19.9,13.36 19.74,14M14.59,19.56C15.19,18.45 15.65,17.25 15.97,16H18.92C17.96,17.65 16.43,18.93 14.59,19.56M14.34,14H9.66C9.56,13.34 9.5,12.68 9.5,12C9.5,11.32 9.56,10.65 9.66,10H14.34C14.43,10.65 14.5,11.32 14.5,12C14.5,12.68 14.43,13.34 14.34,14M12,19.96C11.17,18.76 10.5,17.43 10.09,16H13.91C13.5,17.43 12.83,18.76 12,19.96M8,8H5.08C6.03,6.34 7.57,5.06 9.4,4.44C8.8,5.55 8.35,6.75 8,8M5.08,16H8C8.35,17.25 8.8,18.45 9.4,19.56C7.57,18.93 6.03,17.65 5.08,16M4.26,14C4.1,13.36 4,12.69 4,12C4,11.31 4.1,10.64 4.26,10H7.64C7.56,10.66 7.5,11.32 7.5,12C7.5,12.68 7.56,13.34 7.64,14M12,4.03C12.83,5.23 13.5,6.57 13.91,8H10.09C10.5,6.57 11.17,5.23 12,4.03M18.92,8H15.97C15.65,6.75 15.19,5.55 14.59,4.44C16.43,5.07 17.96,6.34 18.92,8M12,2C6.47,2 2,6.5 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z',
      type: 'public',
    },
    {
      name: 'Test 1',
      label: 'test1',
      svg_path:
        'M16.36,14C16.44,13.34 16.5,12.68 16.5,12C16.5,11.32 16.44,10.66 16.36,10H19.74C19.9,10.64 20,11.31 20,12C20,12.69 19.9,13.36 19.74,14M14.59,19.56C15.19,18.45 15.65,17.25 15.97,16H18.92C17.96,17.65 16.43,18.93 14.59,19.56M14.34,14H9.66C9.56,13.34 9.5,12.68 9.5,12C9.5,11.32 9.56,10.65 9.66,10H14.34C14.43,10.65 14.5,11.32 14.5,12C14.5,12.68 14.43,13.34 14.34,14M12,19.96C11.17,18.76 10.5,17.43 10.09,16H13.91C13.5,17.43 12.83,18.76 12,19.96M8,8H5.08C6.03,6.34 7.57,5.06 9.4,4.44C8.8,5.55 8.35,6.75 8,8M5.08,16H8C8.35,17.25 8.8,18.45 9.4,19.56C7.57,18.93 6.03,17.65 5.08,16M4.26,14C4.1,13.36 4,12.69 4,12C4,11.31 4.1,10.64 4.26,10H7.64C7.56,10.66 7.5,11.32 7.5,12C7.5,12.68 7.56,13.34 7.64,14M12,4.03C12.83,5.23 13.5,6.57 13.91,8H10.09C10.5,6.57 11.17,5.23 12,4.03M18.92,8H15.97C15.65,6.75 15.19,5.55 14.59,4.44C16.43,5.07 17.96,6.34 18.92,8M12,2C6.47,2 2,6.5 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z',
      type: 'public',
    },
    {
      name: 'Test 2',
      label: 'test2',
      svg_path:
        'M16.36,14C16.44,13.34 16.5,12.68 16.5,12C16.5,11.32 16.44,10.66 16.36,10H19.74C19.9,10.64 20,11.31 20,12C20,12.69 19.9,13.36 19.74,14M14.59,19.56C15.19,18.45 15.65,17.25 15.97,16H18.92C17.96,17.65 16.43,18.93 14.59,19.56M14.34,14H9.66C9.56,13.34 9.5,12.68 9.5,12C9.5,11.32 9.56,10.65 9.66,10H14.34C14.43,10.65 14.5,11.32 14.5,12C14.5,12.68 14.43,13.34 14.34,14M12,19.96C11.17,18.76 10.5,17.43 10.09,16H13.91C13.5,17.43 12.83,18.76 12,19.96M8,8H5.08C6.03,6.34 7.57,5.06 9.4,4.44C8.8,5.55 8.35,6.75 8,8M5.08,16H8C8.35,17.25 8.8,18.45 9.4,19.56C7.57,18.93 6.03,17.65 5.08,16M4.26,14C4.1,13.36 4,12.69 4,12C4,11.31 4.1,10.64 4.26,10H7.64C7.56,10.66 7.5,11.32 7.5,12C7.5,12.68 7.56,13.34 7.64,14M12,4.03C12.83,5.23 13.5,6.57 13.91,8H10.09C10.5,6.57 11.17,5.23 12,4.03M18.92,8H15.97C15.65,6.75 15.19,5.55 14.59,4.44C16.43,5.07 17.96,6.34 18.92,8M12,2C6.47,2 2,6.5 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z',
      type: 'public',
    },
    {
      name: 'Account',
      label: 'account',
      svg_path:
        'M12,4A4,4 0 0,1 16,8A4,4 0 0,1 12,12A4,4 0 0,1 8,8A4,4 0 0,1 12,4M12,14C16.42,14 20,15.79 20,18V20H4V18C4,15.79 7.58,14 12,14Z',
      type: 'account',
    },
    {
      name: 'Login',
      label: 'login',
      svg_path:
        'M10,17V14H3V10H10V7L15,12L10,17M10,2H19A2,2 0 0,1 21,4V20A2,2 0 0,1 19,22H10A2,2 0 0,1 8,20V18H10V20H19V4H10V6H8V4A2,2 0 0,1 10,2Z',
      type: 'account',
    },
  ]);

  const [horizontalNavItemList, setHorizontalNavItemList] = useState([]);
  const [horizontalNavItemValue, setHorizontalNavItemValue] = useState(false);

  const [verticalNavItemList, setVerticalNavItemList] = useState([]);
  const [verticalNavItemValue, setVerticalNavItemValue] = useState(false);

  const [navItemNumber, setNavItemNumber] = useState(0);

  const navItemNumberHandler = useCallback(() => {
    // console.log('setNavItemNumber');

    isExtraSmallDevice && setNavItemNumber(0);
    isSmallDevice && setNavItemNumber(1);
    isSmallMediumDevice && setNavItemNumber(2);
    isMediumDevice && setNavItemNumber(3);
    isLargeMediumDevice && setNavItemNumber(4);
    isLargeDevice && setNavItemNumber(5);
  }, [
    isExtraSmallDevice,
    isSmallDevice,
    isSmallMediumDevice,
    isMediumDevice,
    isLargeMediumDevice,
    isLargeDevice,
  ]);

  const navItemListHandler = useCallback(
    (navItems) => {
      // console.log('navItemListHandler: ', linkItemList);
      if (linkItemList.length > 0) {
        const copiedLinkItemList = [...linkItemList];
        const navItemList = copiedLinkItemList.filter(
          (linkItem) => linkItem.type === 'public'
        );

        let verticalNavItems = [];
        let horizontalNavItems = [];
        if (navItems > 0) {
          horizontalNavItems = navItemList.splice(0, navItems);
          verticalNavItems = navItemList;
        } else {
          verticalNavItems = navItemList;
        }

        setHorizontalNavItemList(horizontalNavItems);
        setVerticalNavItemValue(false);
        setVerticalNavItemList(verticalNavItems);
        setHorizontalNavItemValue(false);
      }
    },
    [linkItemList]
  );

  const verticalTabsValueHandler = useCallback(
    (event, newValue) => {
      // console.log('setVerticalNavItemValue: ', verticalNavItemList);
      const tabsValue = verticalNavItemList.map(
        (linkItem) => `/${linkItem.label}`
      );
      const pathIsExistInTabs = tabsValue.includes(newValue);
      if (!pathIsExistInTabs) {
        setVerticalNavItemValue(false);
      } else {
        setHorizontalNavItemValue(false);
        setVerticalNavItemValue(newValue);
      }
    },
    [verticalNavItemList]
  );

  const horizontalTabsValueHandler = useCallback(
    (event, newValue) => {
      // console.log('horizontalTabsValueHandler: ', horizontalNavItemList);
      const tabsValue = horizontalNavItemList.map(
        (linkItem) => `/${linkItem.label}`
      );
      const pathIsExistInTabs = tabsValue.includes(newValue);
      if (!pathIsExistInTabs) {
        setHorizontalNavItemValue(false);
      } else {
        setVerticalNavItemValue(false);
        setHorizontalNavItemValue(newValue);
      }
    },
    [horizontalNavItemList]
  );

  const tabsValueHandler = useCallback(
    (event, newValue) => {
      verticalTabsValueHandler(event, newValue);
      horizontalTabsValueHandler(event, newValue);
    },
    [verticalTabsValueHandler, horizontalTabsValueHandler]
  );

  useEffect(() => {
    navItemListHandler(navItemNumber);
  }, [navItemNumber, navItemListHandler]);

  useEffect(() => {
    navItemNumberHandler();
  }, [navItemNumberHandler]);

  useEffect(() => {
    tabsValueHandler('', location.pathname);
  }, [location.pathname, tabsValueHandler]);

  const isActive = useMemo(
    () => (history, path) => {
      if (history.location.pathname === path) {
        return {
          color: prefersDarkMode
            ? theme.palette.secondary.dark
            : theme.palette.secondary.light,
        };
      } else return { color: '#ffffff' };
    },
    [prefersDarkMode]
  );

  const toggleDrawer = () => {
    setDrawerIsOpen(!drawerIsOpen);
  };

  const Logo = (props) => {
    return (
      <div className={classes.logoContainer}>
        <Button
          component={RouterLink}
          to="/"
          style={isActive(history, '/')}
          className={classes.logo}
          color="inherit"
          onClick={tabsValueHandler}
        >
          <Typography variant="h6">Initcard</Typography>
        </Button>
      </div>
    );
  };

  const VerticalNavItems = (props) => {
    return (
      <Tabs
        orientation="vertical"
        centered
        value={verticalNavItemValue}
        onChange={verticalTabsValueHandler}
        textColor="primary"
        aria-label="icon label tabs"
      >
        {props.itemList.map((linkItem) => {
          return (
            <Tab
              value={`/${linkItem.label}`}
              component={RouterLink}
              to={`/${linkItem.label}`}
              style={isActive(history, `/${linkItem.label}`)}
              key={`${linkItem.label}`}
              icon={
                <SvgIcon>
                  <path d={`${linkItem.svg_path}`} />
                </SvgIcon>
              }
              label={linkItem.label}
              className={classes.verticalTab}
            />
          );
        })}
      </Tabs>
    );
  };

  const HorizontalNavItems = (props) => {
    return (
      <Tabs
        // variant=""
        centered
        value={horizontalNavItemValue}
        onChange={horizontalTabsValueHandler}
        textColor="primary"
        aria-label="icon label tabs"
        className={classes.horizontalNavItems}
      >
        {props.itemList.map((linkItem) => {
          return (
            <Tab
              value={`/${linkItem.label}`}
              component={RouterLink}
              to={`/${linkItem.label}`}
              style={isActive(history, `/${linkItem.label}`)}
              key={`${linkItem.label}`}
              icon={
                <SvgIcon>
                  <path d={`${linkItem.svg_path}`} />
                </SvgIcon>
              }
              // label={linkItem.label}
            />
          );
        })}
      </Tabs>
    );
  };

  const Account = (props) => (
    <>
      {linkItemList
        ? linkItemList
            .filter((linkItem) => linkItem.type === 'account')
            .map((linkItem) => (
              <IconButton
                key={linkItem.label}
                component={RouterLink}
                to={`/${linkItem.label}`}
                style={isActive(history, `/${linkItem.label}`)}
                onClick={tabsValueHandler}
              >
                <SvgIcon>
                  <path d={`${linkItem.svg_path}`} />
                </SvgIcon>
              </IconButton>
            ))
        : null}
    </>
  );

  return (
    <div>
      <AppBar position="fixed">
        <Toolbar>
          {verticalNavItemList.length > 0 ? (
            <IconButton
              edge={false}
              aria-label="menu"
              color="inherit"
              onClick={toggleDrawer}
            >
              <MenuIcon />
              <SwipeableDrawer
                anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                onClose={toggleDrawer}
                open={drawerIsOpen}
                onOpen={toggleDrawer}
              >
                <VerticalNavItems
                  itemList={verticalNavItemList}
                  className={classes.navigationItemsMobile}
                />
              </SwipeableDrawer>
            </IconButton>
          ) : null}
          <Logo />
          <SearchBar />
          {horizontalNavItemList ? (
            <HorizontalNavItems
              itemList={horizontalNavItemList}
              className={classes.navigationItemsDesktop}
            />
          ) : null}
          <Account />
        </Toolbar>
      </AppBar>
      <div className={classes.offset} />
    </div>
  );
});

export default NavBar;
