export const fieldValidation = (value, validations) => {
  let validity = { isValid: true, label: 'checked' };

  if (!validations) {
    return validity;
  }

  if (validations.isRequired) {
    validity =
      value.trim() !== ''
        ? validity
        : { isValid: false, label: 'This field is required' };
  }

  if (validations.minLength) {
    validity =
      value.length >= validations.minLength
        ? validity
        : {
            isValid: false,
            label: `Minimum ${validations.minLength} characters`,
          };
  }

  if (validations.maxLength) {
    validity =
      value.length <= validations.maxLength
        ? validity
        : {
            isValid: false,
            label: `Maximum ${validations.maxLength} characters`,
          };
  }

  return validity;
};
