import React, { useState } from 'react';
import {
  TextField,
  SvgIcon,
  Grid,
  InputAdornment,
  IconButton,
  Typography,
} from '@material-ui/core';
import LoadingButton from '@material-ui/lab/LoadingButton';
import { makeStyles } from '@material-ui/core/styles';
import { useFormik } from 'formik';
import * as yup from 'yup';
import ToggleIcon from 'material-ui-toggle-icon';

import { grey } from '@material-ui/core/colors';

import {
  alertIcon,
  checkIcon,
  closeIcon,
  visibilityOffIcon,
  visibilityOnIcon,
} from '../../assets/svg/SvgIcons';

const useStyles = makeStyles((theme) => ({
  inputContainer: {
    minWidth: theme.breakpoints.values.xxs,
    maxWidth: theme.breakpoints.values.sm,
    backgroundColor: theme.palette.mode === 'dark' ? grey[800] : grey[100],
    [theme.breakpoints.up('sm')]: {
      borderRadius: theme.spacing(1),
      marginTop: theme.spacing(3),
    },
    padding: theme.spacing(3),
  },
  buttonSpacing: {
    marginTop: theme.spacing(1),
  },
}));

const AccountContainer = () => {
  const classes = useStyles();

  const [isSignIn, setIsSignIn] = useState(true);

  const [isPendingSignIn, setIsPendingSignIn] = useState(false);

  const [isPendingRequestPassword, setIsPendingRequestPassword] = useState(
    false
  );

  const [isPendingSignUp, setIsPendingSignUp] = useState(false);

  const [isPendingChangeType, setIsPendingChangeType] = useState(false);

  const [passwordType, setPasswordType] = useState('password');
  const [toggleVisibilityIcon, setToggleVisibilityIcon] = useState(false);

  const validationSchema = yup.object({
    email: yup
      .string('Enter your email')
      .email('Enter a valid email')
      .required('Email is required'),
    password: yup
      .string('Enter your password')
      .min(8, 'Minimum 8 characters length')
      .required('Password is required'),
  });

  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      alert(JSON.stringify(values, null, 2));
    },
  });

  const signInHandler = () => {};

  const forgetPasswordHandler = () => {
    setIsPendingRequestPassword(true);
    setTimeout(() => {
      setIsPendingRequestPassword(false);
    }, 1000);
  };

  const signUpHandler = () => {};

  const changeTypeHandler = () => {
    setIsPendingChangeType(true);
    setIsSignIn(!isSignIn);
    setIsPendingChangeType(false);
  };

  const resetInputHandler = (field, value) => {
    formik.setFieldValue(field, value);
  };

  const passwordToggleHandler = () => {
    setPasswordType(passwordType === 'password' ? 'text' : 'password');
    setToggleVisibilityIcon(!toggleVisibilityIcon);
  };

  return (
    <Grid container direction="row" justifyContent="center" alignItems="center">
      <Grid
        container
        direction="column"
        justifyContent="flex-start"
        alignItems="center"
        className={classes.inputContainer}
      >
        <Grid item>
          <TextField
            id="email"
            label="Email"
            variant="filled"
            color="secondary"
            margin="normal"
            type="email"
            required
            error={formik.touched.email && Boolean(formik.errors.email)}
            value={formik.values.email}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            helperText={
              formik.touched.email && Boolean(!formik.errors.email) ? (
                <span>
                  <SvgIcon style={{ width: 12, height: 12, marginRight: 8 }}>
                    <path d={checkIcon.path} />
                  </SvgIcon>
                  Checked
                </span>
              ) : formik.touched.email && Boolean(formik.errors.email) ? (
                <span>
                  <SvgIcon style={{ width: 12, height: 12, marginRight: 8 }}>
                    <path d={alertIcon.path} />
                  </SvgIcon>
                  {formik.errors.email}
                </span>
              ) : (
                '*Required'
              )
            }
            InputProps={{
              endAdornment: (
                <InputAdornment>
                  <IconButton
                    disabled={Boolean(!formik.values.email)}
                    onClick={() => resetInputHandler('email', '')}
                  >
                    <SvgIcon>
                      <path d={closeIcon.path} />
                    </SvgIcon>
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </Grid>
        <Grid item>
          <TextField
            id="password"
            label="Password"
            variant="filled"
            color="secondary"
            margin="normal"
            type={passwordType}
            required
            error={formik.touched.password && Boolean(formik.errors.password)}
            value={formik.values.password}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            helperText={
              formik.touched.password && Boolean(!formik.errors.password) ? (
                <span>
                  <SvgIcon style={{ width: 12, height: 12, marginRight: 8 }}>
                    <path d={checkIcon.path} />
                  </SvgIcon>
                  Checked
                </span>
              ) : formik.touched.password && Boolean(formik.errors.password) ? (
                <span>
                  <SvgIcon style={{ width: 12, height: 12, marginRight: 8 }}>
                    <path d={alertIcon.path} />
                  </SvgIcon>
                  {formik.errors.password}
                </span>
              ) : (
                '*Minimum 8 characters'
              )
            }
            InputProps={{
              endAdornment: (
                <InputAdornment>
                  <IconButton
                    disabled={Boolean(!formik.values.password)}
                    onClick={passwordToggleHandler}
                  >
                    <ToggleIcon
                      on={toggleVisibilityIcon}
                      onIcon={
                        <SvgIcon>
                          <path d={visibilityOnIcon.path} />
                        </SvgIcon>
                      }
                      offIcon={
                        <SvgIcon>
                          <path d={visibilityOffIcon.path} />
                        </SvgIcon>
                      }
                    />
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </Grid>

        <Grid item style={{ display: 'flex' }}>
          {isSignIn ? (
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d="M19 13C19.34 13 19.67 13.04 20 13.09V10C20 8.9 19.11 8 18 8H17V6C17 3.24 14.76 1 12 1S7 3.24 7 6V8H6C4.9 8 4 8.89 4 10V20C4 21.11 4.89 22 6 22H13.81C13.3 21.12 13 20.1 13 19C13 15.69 15.69 13 19 13M9 6C9 4.34 10.34 3 12 3S15 4.34 15 6V8H9V6M12 17C10.9 17 10 16.11 10 15S10.9 13 12 13C13.1 13 14 13.89 14 15C14 16.11 13.11 17 12 17M22.5 17.25L17.75 22L15 19L16.16 17.84L17.75 19.43L21.34 15.84L22.5 17.25Z" />
                </SvgIcon>
              }
              color="secondary"
              onClick={signInHandler}
              pending={isPendingSignIn}
              disabled={
                !formik.touched.email ||
                Boolean(formik.errors.email) ||
                !formik.touched.password ||
                Boolean(formik.errors.password)
              }
              pendingPosition="start"
              variant="contained"
              className={classes.buttonSpacing}
              type="submit"
            >
              Sign In
            </LoadingButton>
          ) : (
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d="M13 17H17V14L22 18.5L17 23V20H13V17M20 4H4A2 2 0 0 0 2 6V18A2 2 0 0 0 4 20H11.35A5.8 5.8 0 0 1 11 18A6 6 0 0 1 22 14.69V6A2 2 0 0 0 20 4M20 8L12 13L4 8V6L12 11L20 6Z" />
                </SvgIcon>
              }
              color="secondary"
              onClick={signUpHandler}
              pending={isPendingSignUp}
              disabled={
                !formik.touched.email ||
                Boolean(formik.errors.email) ||
                !formik.touched.password ||
                Boolean(formik.errors.password)
              }
              pendingPosition="start"
              variant="contained"
              className={classes.buttonSpacing}
            >
              Sign Up
            </LoadingButton>
          )}
        </Grid>
        <Grid item style={{ display: 'flex' }}>
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d="M8,10V13H14V18H8V21L2,15.5L8,10M22,8.5L16,3V6H10V11H16V14L22,8.5Z" />
              </SvgIcon>
            }
            color="secondary"
            onClick={changeTypeHandler}
            pending={isPendingChangeType}
            pendingPosition="start"
            variant="outlined"
            className={classes.buttonSpacing}
          >
            Change to {isSignIn ? 'Sign Up' : 'SignIn'}
          </LoadingButton>
        </Grid>
        {isSignIn ? (
          <Grid item style={{ display: 'flex' }}>
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d="M12,1A5,5 0 0,0 7,6V8H6A2,2 0 0,0 4,10V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V10A2,2 0 0,0 18,8H17V6A5,5 0 0,0 12,1M12,2.9C13.71,2.9 15.1,4.29 15.1,6V8H8.9V6C8.9,4.29 10.29,2.9 12,2.9M12.19,10.5C13.13,10.5 13.88,10.71 14.42,11.12C14.96,11.54 15.23,12.1 15.23,12.8C15.23,13.24 15.08,13.63 14.79,14C14.5,14.36 14.12,14.64 13.66,14.85C13.4,15 13.23,15.15 13.14,15.32C13.05,15.5 13,15.72 13,16H11C11,15.5 11.1,15.16 11.29,14.92C11.5,14.68 11.84,14.4 12.36,14.08C12.62,13.94 12.83,13.76 13,13.54C13.14,13.33 13.22,13.08 13.22,12.8C13.22,12.5 13.13,12.28 12.95,12.11C12.77,11.93 12.5,11.85 12.19,11.85C11.92,11.85 11.7,11.92 11.5,12.06C11.34,12.2 11.24,12.41 11.24,12.69H9.27C9.22,12 9.5,11.4 10.05,11.04C10.59,10.68 11.3,10.5 12.19,10.5M11,17H13V19H11V17Z" />
                </SvgIcon>
              }
              color="secondary"
              onClick={forgetPasswordHandler}
              pending={isPendingRequestPassword}
              pendingPosition="start"
              variant="outlined"
              className={classes.buttonSpacing}
            >
              Forget Password
            </LoadingButton>
          </Grid>
        ) : null}
      </Grid>
    </Grid>
  );
};

export default AccountContainer;
